@import url(https://fonts.googleapis.com/css?family=Chewy&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

:root {
  font-size: 62.5%; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.6rem;
  line-height: 1.3; }

a {
  text-decoration: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal; }

@-webkit-keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.home-wrapper header.intro {
  text-align: center;
  padding: 90px 60px;
  opacity: 0;
  -webkit-animation: fadein 500ms ease-in-out 600ms;
          animation: fadein 500ms ease-in-out 600ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }
  .home-wrapper header.intro h1 {
    font-family: "Chewy", cursive;
    font-size: 4.8rem;
    color: #333333; }
  .home-wrapper header.intro p {
    font-size: 1.8rem;
    color: #666666; }

.home-wrapper ul.list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  padding: 0 20px 20px; }
  .home-wrapper ul.list .list-item {
    height: 300px;
    flex: 1 1;
    min-width: 100%; }
    @media only screen and (min-width: 577px) {
      .home-wrapper ul.list .list-item {
        flex: 0.5 1;
        min-width: 50%; } }
    @media only screen and (min-width: 991px) {
      .home-wrapper ul.list .list-item {
        flex: 0.33 1;
        min-width: 33.33%; } }
    @media only screen and (min-width: 1200px) {
      .home-wrapper ul.list .list-item {
        flex: 0.25 1;
        min-width: 25%; } }
    .home-wrapper ul.list .list-item a {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 40px 20px;
      position: relative;
      height: 100%;
      width: 100%;
      -webkit-transition: all 600ms ease-in-out;
      transition: all 600ms ease-in-out;
      opacity: 0;
      -webkit-animation: fadein 500ms ease-in-out 600ms;
              animation: fadein 500ms ease-in-out 600ms;
      -webkit-animation-fill-mode: forwards;
              animation-fill-mode: forwards; }
      .home-wrapper ul.list .list-item a img {
        height: 80%;
        max-height: 400px; }
      .home-wrapper ul.list .list-item a h3 {
        font-size: 3rem;
        font-family: "Chewy", cursive;
        color: rgba(0, 0, 0, 0.6); }
      .home-wrapper ul.list .list-item a .bg {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: -1;
        border-radius: 30px;
        opacity: 0;
        -webkit-transform: scale(0.6);
                transform: scale(0.6);
        -webkit-transform-origin: bottom;
                transform-origin: bottom;
        -webkit-transition: all 0.4s;
        transition: all 0.4s; }
    .home-wrapper ul.list .list-item:hover a .bg {
      opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1); }
    .home-wrapper ul.list .list-item:hover a img {
      -webkit-animation: jump 0.5s;
              animation: jump 0.5s; }

@-webkit-keyframes jump {
  0% {
    -webkit-transform: translateY(0px) scale(1, 0.9);
            transform: translateY(0px) scale(1, 0.9); }
  40% {
    -webkit-transform: translateY(-60px) scale(0.9, 1.1);
            transform: translateY(-60px) scale(0.9, 1.1); }
  100% {
    -webkit-transform: translateY(0px) scale(1, 1);
            transform: translateY(0px) scale(1, 1); } }

@keyframes jump {
  0% {
    -webkit-transform: translateY(0px) scale(1, 0.9);
            transform: translateY(0px) scale(1, 0.9); }
  40% {
    -webkit-transform: translateY(-60px) scale(0.9, 1.1);
            transform: translateY(-60px) scale(0.9, 1.1); }
  100% {
    -webkit-transform: translateY(0px) scale(1, 1);
            transform: translateY(0px) scale(1, 1); } }

.details-wrapper {
  display: flex;
  height: 100vh; }
  @media only screen and (max-width: 991px) {
    .details-wrapper {
      display: block; } }
  .details-wrapper .img-wrapper {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 40vw;
    height: 100vh;
    padding: 40px;
    -webkit-transition: all 600ms ease-in-out;
    transition: all 600ms ease-in-out; }
    @media only screen and (max-width: 991px) {
      .details-wrapper .img-wrapper {
        max-width: 100%;
        height: 50vh; } }
    .details-wrapper .img-wrapper img {
      height: 80%;
      max-height: 400px; }
  .details-wrapper .details {
    flex: 1 1;
    padding: 40px;
    opacity: 0;
    -webkit-animation: fadein 500ms ease-in-out 600ms;
            animation: fadein 500ms ease-in-out 600ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    overflow: auto; }
    .details-wrapper .details .detail-box {
      margin-bottom: 40px; }
      .details-wrapper .details .detail-box h2 {
        font-size: 6rem;
        font-family: "Chewy", cursive;
        margin-bottom: 20px;
        color: #333333; }
      .details-wrapper .details .detail-box h3 {
        font-family: "Chewy", cursive;
        font-size: 1.8rem;
        color: #333333; }
      .details-wrapper .details .detail-box p {
