    .details-wrapper .details .back {
      display: flex;
      margin: 0 0 40px 0;
      flex-direction: row;
      color: #999999;
      align-items: center;
      cursor: pointer;
      letter-spacing: 2px;
      -webkit-transition: all 300ms;
      transition: all 300ms; }
      .details-wrapper .details .back:hover {
        color: #666666;
        letter-spacing: 0; }
      @media only screen and (max-width: 991px) {
        .details-wrapper .details .back {
          color: #666666;
          position: absolute;
          top: 0;
          left: 0;
          margin: 0;
          padding: 10px;
          background-color: rgba(255, 255, 255, 0.8); } }
      .details-wrapper .details .back img {
        margin-right: 20px; }

